.container {
  max-width: 480px;
  margin: 20px auto;
  padding: 0 15px;
  font-family: "Noto Sans TC", sans-serif;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.form select,
.form input,
.form button {
  width: 100%;
  padding: 10px 12px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  transition: border-color 0.3s;
}

.form select:focus,
.form input:focus {
  border-color: #4a90e2;
  outline: none;
}

.form button {
  font-weight: 600;
  background-color: #4a90e2;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.form button:hover {
  background-color: #357abd;
}

.respMsg {
  margin-top: 12px;
  color: green;
  font-weight: 500;
  text-align: center;
}

.errorMsg {
  margin-top: 12px;
  color: red;
  font-weight: 500;
  text-align: center;
}

.nearestOrg {
  margin-top: 20px;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background-color: #f9f9f9;
  font-size: 1rem;
}

/* 響應式調整 */
@media (max-width: 480px) {
  .container {
    padding: 0 10px;
  }
  .form select,
  .form input,
  .form button {
    font-size: 0.9rem;
    padding: 8px 10px;
  }
  .nearestOrg {
    font-size: 0.9rem;
  }
}
